import { firestore } from 'firebase';

const collName = 'clients';

const clients = {
    namespaced: true,
    state: {
        clients: [],
    },
    getters: {
        getClients: (state) => {
            return state.clients;
        }
    },
    mutations: {
        SET_CLIENTS(state, clients) {
            state.clients = clients;
        },
        ADD_CLIENT(state, client) {
            state.clients.push(client);
        },
        UPDATE_CLIENT(state, payload) {
            Object.assign(state.clients[payload.index], payload.client);
        },
        REMOVE_CLIENT(state, client) {
            const index = state.clients.indexOf(client);
            state.clients.splice(index, 1);
        }
    },
    actions: {
        async addClient({commit}, client) {
            const docRef = await firestore().collection(collName).add(client);
            client.id = docRef.id;
            commit('ADD_CLIENT', client);
        },
        async loadClients({commit}) {
            console.log(collName);
            const querySnapshot = await firestore().collection(collName).get();
            let client;
            let clients = querySnapshot.docs.map(function (doc) {
                client = doc.data();
                client.id = doc.id;
                return client;
            });
            commit('SET_CLIENTS', clients);
        },
        async updateClient({commit}, payload) {
            await firestore().collection(collName).doc(payload.client.id).set(payload.client);
            commit('UPDATE_CLIENT', payload);
        },
        async removeClient({commit}, client) {
            await firestore().collection(collName).doc(client.id).delete();
            commit('REMOVE_CLIENT', client);
        }
    }
}

export default clients;