import { firestore } from 'firebase';

const collName = 'products';

const products = {
    namespaced: true,
    state: {
        products: [],
    },
    getters: {
        getProducts: (state) => {
            return state.products;
        }
    },
    mutations: {
        SET_PRODUCTS(state, products) {
            state.products = products;
        },
        ADD_PRODUCT(state, product) {
            state.products.push(product);
        },
        UPDATE_PRODUCT(state, payload) {
            Object.assign(state.products[payload.index], payload.product);
        },
        REMOVE_PRODUCT(state, product) {
            const index = state.products.indexOf(product);
            state.products.splice(index, 1);
        }
    },
    actions: {
        async addProduct({commit}, product) {
            const docRef = await firestore().collection(collName).add(product);
            product.id = docRef.id;
            commit('ADD_PRODUCT', product);
        },
        async loadProducts({commit}) {
            console.log(collName);
            const querySnapshot = await firestore().collection(collName).get();
            let product;
            let products = querySnapshot.docs.map(function (doc) {
                product = doc.data();
                product.id = doc.id;
                return product;
            });
            commit('SET_PRODUCTS', products);
        },
        async updateProduct({commit}, payload) {
            await firestore().collection(collName).doc(payload.product.id).set(payload.product);
            commit('UPDATE_PRODUCT', payload);
        },
        async removeProduct({commit}, product) {
            await firestore().collection(collName).doc(product.id).delete();
            commit('REMOVE_PRODUCT', product);
        }
    }
}

export default products;