import { firestore } from 'firebase';

const collName = 'shoes';

const shoes = {
    namespaced: true,
    state: {
        shoes: [],
        clients: [],
        clientOptions: [],
        clientValues: [],
    },
    getters: {
        getShoes: (state) => {
            return state.shoes;
        },
        getClientOptions: (state) => {
            return state.clientOptions;
        }, 
        getClientValues: (state) => {
            return state.clientValues;
        }
    },
    /*setters: {
        setClientValue: (state, value) => {
            state.
        }
    }, */
    mutations: {
        SET_SHOES(state, shoes) {
            state.shoes = shoes;
        },
        ADD_SHOE(state, shoe) {
            state.shoes.push(shoe);
        },
        UPDATE_SHOE(state, payload) {
            Object.assign(state.shoes[payload.index], payload.shoe);
        },
        REMOVE_SHOE(state, shoe) {
            const index = state.shoes.indexOf(shoe);
            state.shoes.splice(index, 1);
        }, 
        SET_CLIENTS(state, clients){
            state.clients = clients;
            state.clientOptions = clients.map(c => {
                return { 'text': c.name, 'value': c.id }
            });
            
        },
    },
    actions: {
        async addShoe({commit}, shoe) {
            const docRef = await firestore().collection(collName).add(shoe);
            shoe.id = docRef.id;
            commit('ADD_SHOE', shoe);
        },
        async loadShoes({commit}) {
            
            //loadClients();

            const querySnapshott = await firestore().collection('clients').get();
            let client;
            let clients = querySnapshott.docs.map(function (doc) {
                client = doc.data();
                client.id = doc.id;

                return client;
                
            });

            commit('SET_CLIENTS', clients);
            
            const querySnapshot = await firestore().collection(collName).get();
            let shoe;
            let shoes = querySnapshot.docs.map(function (doc) {
                shoe = doc.data();
                shoe.id = doc.id; 
                shoe.client = clients.find(c => c.id == shoe.client).name;
                
                return shoe;
                
            });
            commit('SET_SHOES', shoes);
        },
        async updateShoe({commit}, payload) {
            await firestore().collection(collName).doc(payload.shoe.id).set(payload.shoe);
            commit('UPDATE_SHOE', payload);
        },
        async removeShoe({commit}, shoe) {
            await firestore().collection(collName).doc(shoe.id).delete();
            commit('REMOVE_SHOE', shoe);
        }
    }
}

export default shoes;